
Template.__checkName("PojedynczeZadanie");
Template["PojedynczeZadanie"] = new Template("Template.PojedynczeZadanie", (function() {
  var view = this;
  return HTML.DIV({
    "class": "lista-zadan"
  }, "\n    ", HTML.H3(Blaze.View("lookup:zadanie.nazwa", function() {
    return Spacebars.mustache(Spacebars.dot(view.lookup("zadanie"), "nazwa"));
  })), "\n    ", HTML.P(Blaze.View("lookup:zadanie.opis", function() {
    return Spacebars.mustache(Spacebars.dot(view.lookup("zadanie"), "opis"));
  })), "\n    ", Blaze.Each(function() {
    return Spacebars.call(Spacebars.dot(view.lookup("zadanie"), "czynnosci"));
  }, function() {
    return [ "\n      ", HTML.SPAN({
      "class": "czynnosc"
    }, Blaze.View("lookup:nazwa", function() {
      return Spacebars.mustache(view.lookup("nazwa"));
    }), " - ", Blaze.View("lookup:opis", function() {
      return Spacebars.mustache(view.lookup("opis"));
    })), "\n    " ];
  }), "\n  ");
}));
