Template.__checkName("ListaZadanWykonanych");
Template["ListaZadanWykonanych"] = new Template("Template.ListaZadanWykonanych", (function() {
  var view = this;
  return HTML.DIV({
    "class": "lista-zadan"
  }, "\n    ", HTML.H3(Blaze.View("lookup:nazwa", function() {
    return Spacebars.mustache(view.lookup("nazwa"));
  })), "\n    ", HTML.P(Blaze.View("lookup:opis", function() {
    return Spacebars.mustache(view.lookup("opis"));
  })), "\n  ");
}));
