
Template.__checkName("Zadania");
Template["Zadania"] = new Template("Template.Zadania", (function() {
  var view = this;
  return [ Blaze.If(function() {
    return Spacebars.dataMustache(Spacebars.dot(view.lookup("$"), "Session", "get"), "noweZadanie");
  }, function() {
    return [ "\n    ", Spacebars.include(view.lookupTemplate("NoweZadanie")), "\n  " ];
  }, function() {
    return [ "\n    ", HTML.BUTTON({
      "class": "nowe-zadanie"
    }, "Nowe Zadanie"), "\n  " ];
  }), "\n  ", HTML.SECTION({
    "class": "zadania"
  }, "\n    ", Blaze.If(function() {
    return Spacebars.call(view.templateInstance().subscriptionsReady());
  }, function() {
    return [ "\n      ", Blaze.Each(function() {
      return Spacebars.call(view.lookup("zadania"));
    }, function() {
      return [ "\n        ", Spacebars.include(view.lookupTemplate("Zadanie")), "\n      " ];
    }), "\n    " ];
  }, function() {
    return [ "\n      ", HTML.P("Loading"), "\n    " ];
  }), "\n  ") ];
}));
